.content-title-coin {
  color: white;
  display: flex;
  margin-bottom: 6rem;
  position: relative;
  width: 80%;

}

.content-no-flip-text {
  color: rgba(255, 255, 255, 0.9);
  font-family: 'Roboto', sans-serif;
  font-weight: 900;
  font-size: 6vw;
  letter-spacing: 4px;
  text-transform: uppercase;
  position: relative;
  margin: 0 auto;
}

.text-flip {
  display: inline-block;
  white-space: nowrap;
  position: absolute; 
  margin-left: 1.5rem;
  opacity: 0;
  animation: rotate-word 20s 0s linear infinite;
}

.text-flip:nth-child(2) {
  animation-delay: 2.5s
}
.text-flip:nth-child(3) {
  animation-delay: 5s
}
.text-flip:nth-child(4) {
  animation-delay: 7.5s
}
.text-flip:nth-child(5) {
  animation-delay: 10s
}
.text-flip:nth-child(6) {
  animation-delay: 12.5s
}
.text-flip:nth-child(7) {
  animation-delay: 15s
}
.text-flip:nth-child(8) {
  animation-delay: 17.5s
}



@keyframes rotate-word {
  0% {
      opacity: 0;
  }
  1% {
      transform: translateY(-200px);
  }
  4% {
      opacity: 0;
  }
  5% {
      opacity: 1;
      transform: translateY(0px);
  }
  17% {
      opacity: 1;
      transform: translateY(0px);
  }
  19% {
      opacity: 0;
  }
  20% {
      transform: translateY(200px);
  }
  80% {
      opacity: 0;
  }
  100% {
      opacity: 0;
  }
}

@media only screen and (max-width: 760px),
(min-device-width: 768px) and (max-device-width: 1024px)  {
  .content-no-flip-text {
    padding-left: 0vw;
  }
}